.disclaimer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition: -webkit-transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  -o-transition: transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition: transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition: transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22), -webkit-transform 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  -webkit-transform: translate3d(0,100%,0);
          transform: translate3d(0,100%,0);
}

.disclaimer--visible {
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1), -webkit-transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}

.disclaimer__text {
  margin: 0;
  color: white;
}

.disclaimer__link:link,
.disclaimer__link:visited,
.disclaimer__link:hover,
.disclaimer__link:focus,
.disclaimer__link:active {
  color: white;
}



